<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import { 
            debug,
            ButtonIcon,
            IconSvg,
            IconToggleSvg,
            Indent,
            InlineInput 
  } from "../../Braintapper"


  import { ProgressPopover,  DueDatePopover, ListItemMenu} from "../../Application"


  




  
  export list = undefined
  export listId = undefined



  goToList = ()->
    location.href="#/lists/#{listId}"


  toggleExpanded = (item)->
    console.log "toggle Expanded"
    item.is_expanded_radar = !item.is_expanded_radar
    dispatch "update", {uuid: item.uuid, is_expanded_radar: item.is_expanded_radar, ancestry: item.ancestry , list_id: listId}

  

  updateListItem = (e)->
    if listId?
      console.log "top event listid: #{listId}"
      e.detail["list_id"] = listId
    dispatch "update", e.detail



  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false


</script>


{#each list.list_items as listItem, index}
  
    
    <div type="radar-item">
      <div flex="initial" style="width: {listItem.ancestry.length * 32}px">
        <Indent level={listItem.ancestry.length} width={32}/>
      </div>
          
      <div collapsible>

      {#if listItem.list_items.length > 0}
        <ButtonIcon on:click={toggleExpanded(listItem)}>
          <IconToggleSvg bind:value={listItem.is_expanded_radar} whenTrue="expanded" whenFalse="collapsed"/>
        </ButtonIcon>
      {/if}
      </div>    
      <div layout="row" flex>
        <div layout="row" item-interior>


          <div flex="initial" progress>
            <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
          </div>

          <div flex ellipsis main>
            
              {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
              {:else}<span ellipsis>{listItem.name || "No Name"}</span>&nbsp;
              {/if}            
              {#if listItem.is_ongoing}
                &nbsp;
                <span><IconSvg icon="ongoing" size="12"/></span>
              {/if}
            

          </div>
          <div flex="initial">

            <!-- due date menu -->
            
            <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
            
          </div>
        </div>
      </div>
      <div flex="initial" menu show-on-hover>
        <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar">
          <IconSvg icon="menu" classes="menu"/>
        </ListItemMenu>
      </div>


    </div>



    <!-- children -->
    {#if listItem.is_expanded_radar }
      <svelte:self bind:list={listItem} {listId} on:update={updateListItem} on:destroy={destroyDispatcher} />
    {/if}
  
{/each}


<style global lang="sass">
  [type="radar-item"]

    // border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 64px)
    max-width: calc(100% + 64px)
    margin-left: -32px // 24+8px for icon cols


    [item-interior]
      width: calc(100%)
      max-width: calc(100%)

    [handle], [menu], [progress] // set cell wdiths
      width: 32px


    [menu]
      text-align: center

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [show-on-hover]
      svg
        path
          fill: transparent
      &[is-showing="true"]
        svg
          path
            fill: var(--blue) !important
      
    



    [collapsible]
      width: 32px
      line-height: 24px
      svg, svg.menu
        display: inline


    &:hover

      background: #fcfcfc

      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)      
      [handle], [collapsible], [draggable-item-menu]
        svg, svg.menu
          display: inline


      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)



      [collapsible]
        button[icon]
          padding: 0px !important
        svg
          path.fill
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)


</style>
